import { useState, useEffect } from 'react';

//     
//        
export const useAdminAccess = () => {
  const [isAdmin, setIsAdmin] = useState<boolean>(false);
  const [isLoading, setIsLoading] = useState<boolean>(true);

  useEffect(() => {
    checkAdminAccess();
  }, []);

  const checkAdminAccess = () => {
    try {
      //  localStorage    
      const adminFlag = localStorage.getItem('rulplus_admin_access');
      const isAdminUser = adminFlag === 'true';
      
      setIsAdmin(isAdminUser);
    } catch (error) {
      console.warn('   :', error);
      setIsAdmin(false);
    } finally {
      setIsLoading(false);
    }
  };

  const enableAdminMode = () => {
    try {
      localStorage.setItem('rulplus_admin_access', 'true');
      setIsAdmin(true);
      console.log('   ');
      
      //   toast 
      if (typeof window !== 'undefined' && (window as any).toast) {
        (window as any).toast.success('   - +!');
      }
    } catch (error) {
      console.error('   :', error);
    }
  };

  const disableAdminMode = () => {
    try {
      localStorage.removeItem('rulplus_admin_access');
      setIsAdmin(false);
      console.log('   ');
      
      //   toast 
      if (typeof window !== 'undefined' && (window as any).toast) {
        (window as any).toast.info('   -.   !');
      }
    } catch (error) {
      console.error('   :', error);
    }
  };

  const toggleAdminMode = () => {
    if (isAdmin) {
      disableAdminMode();
    } else {
      enableAdminMode();
    }
  };

  return {
    isAdmin,
    isLoading,
    enableAdminMode,
    disableAdminMode,
    toggleAdminMode,
    checkAdminAccess
  };
};

export default useAdminAccess; 